<template>
  <div class="apply-wrapper-info">
    <app-header title="审批中" />
    <div class="tip">
      您已成功提交申请，请等候审核人审批，审批通过后，我们将消息通知您
    </div>
    <app-fold
      class="wg-contract-group mt10"
      title="审批流程"
      :hasChecked="false"
    >
      <div class="flow-wrapper">
        <div class="flow-item" v-for="(item, index) in list" :key="index">
          <div class="item-left" :class="`status-${item.date ? 'ok' : 'wait'}`">
            <van-icon :name="item.date ? 'checked' : 'more'" />
          </div>
          <div class="item-content">
            <div class="content-info">
              <div class="content-left">
                <span class="item-name">{{ item.name }}</span>
                <span
                  class="item-label"
                  :class="`status-${item.date ? 'ok' : 'wait'}`"
                  >{{ item.status }}</span
                >
              </div>
              <div class="content-right">
                {{ item.date }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </app-fold>
  </div>
</template>

<script>
import AppFold from '@components/app-fold';
import { getFlow } from '@src/api/user-state';

export default {
  components: {
    AppFold,
  },

  data() {
    return {
      list: [],
    };
  },

  activated() {
    this.init();
  },

  methods: {
    init() {
      this.list = [];
      let { applyId, projectId } = {
        ...this.$route.params,
      };

      if (applyId && projectId) {
        return getFlow({
          apply_id: applyId,
          project_id: projectId,
        }).then(data => {
          this.list = [];
          this.list.push({
            name: data.apply_username,
            date: data.apply_time,
            status: '发起',
          });

          this.list.push({
            name: data.approve_username,
            date: '',
            status: '待审批',
          });
        });
      }

      let { applyUserName, approvalUserName, createdOn } = {
        ...this.$route.params,
      };

      if (applyUserName && approvalUserName && createdOn) {
        this.list = [
          {
            name: applyUserName,
            date: createdOn,
            status: '发起',
          },
          {
            name: approvalUserName,
            date: '',
            status: '待审批',
          },
        ];
        return;
      }

      this.$toast('流程不存在，请联系系统管理员');
      this.$router.go(-1);
    },
  },
};
</script>

